.header h1 {
  font-family: "Lilita One", cursive;
  font-size: 30px;
  color: #e4d339;
}

.secundary__section {
  font-family: "Montserrat", sans-serif;
  align-items: center;
  display: flex;
}

.author__img {
  height: 75px;
  width: 75px;
  margin: auto;
}

.author__title {
  font-size: 10px;
  font-family: "Lilita One", cursive;
}

.primary__section {
  font-family: "Montserrat", sans-serif;
}

.footer {
  position: relative;
  color: #2e7fc2;
  font-family: "Lilita One", cursive;
  font-size: 25px;
  text-align: center;
}

.container__page {
  display: flex;
}

@media all and (min-width: 768px) {
  .secundary__section {
    display: flex;
    flex-direction: column;
  }
}

@media all and (max-width: 768px) {
  .container__page {
    display: inline-block;
  }
}
